import { useUnit } from "effector-react";
import { ErrorSnackbar, RemoveSnackbar, SuccesSnackbar } from "../../components/snackbars";
import { $statusActiveSnackbar, setSnackbar } from "../../store/activeModal";
import React from "react";

type Props = {
  children: React.ReactNode;
}

type SnackbarMap = { [key: string]: JSX.Element };

const snackbars: SnackbarMap = {
  'fav-adding': <SuccesSnackbar id="fav-adding" text="   " />,
  'fav-adding-error': <ErrorSnackbar id="fav-adding-error" text="    " />,
  'fav-remove': <RemoveSnackbar id="fav-remove" text="   " />,
  'fav-remove-error': <ErrorSnackbar id="fav-remove-error" text="    " />,
  'book-remove': <SuccesSnackbar id="fav-adding" text="  " />,
  'book-update': <SuccesSnackbar id="fav-adding" text="  " />,
  'error': <ErrorSnackbar id="fav-remove-error" text="     " />,
  'image-update': <SuccesSnackbar id="image-update" text="  " />,
  'image-update-error': <ErrorSnackbar id="image-update-error" text="    " />,
};

const SnackBarProvider = ({ children }: Props) => {
  const status = useUnit($statusActiveSnackbar)

  const activeSnackbar = status ? snackbars[status] : null;

  return (
    <>
      {children}
      {activeSnackbar}
    </>
  )
}

export default SnackBarProvider